.grid-container {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 16px;
  max-width: 650px;
}

.card {
  border-radius: 1.5rem;
  padding: 5px;
  text-align: center;
  text-decoration: none;

}


.card.black-theme {
  background-color: rgba(51, 51, 51, 0.84);
}

.card.white-theme {
  background-color: rgb(255, 255, 255);
}

.card figure {
  border-radius: 1.5rem;
  overflow: hidden;
}

.card h1.highlight {
  text-align: start;
  font-size: 1.125rem;
}

figure {
  margin: 0;
}

figure img {
  max-width: 100%;
  height: auto;
  transition: transform 0.3s ease;
}

.card:hover img {
  transform: scale(1.1); 
}

figcaption {
  margin-top: 8px;
  font-style: italic;
  color: #666;
  padding: 10px 5%;
}

figcaption p {
  display: -webkit-box;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
  line-height: 1.5;
  max-height: calc(1.5em * 5);
  margin: 0;
  padding: 0;
}

.span-two {
  grid-column: span 2;
}

.button-container{
  display: flex;
  justify-content: center;
}

.btn-ver-menos,
.btn-ver-todos {
  text-align: center;
  margin-top: 16px;
  padding: 10px 20px;
  background: none;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
}



.btn-ver-menos::after,
.btn-ver-todos::after {
  content: '';
  position: absolute;
  width: 0;
  height: 2px;
  background: white;
  bottom: 0;
  left: 0;
  transition: width 0.3s ease;
}
.btn-ver-menos:hover::after,
.btn-ver-todos:hover::after {
  width: 100%; 
}

.technologies-list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex; 
  flex-wrap: wrap;
}

.technologies-list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-wrap: wrap;
}

.technologies-list li {
  text-decoration: none; /* Asegura que no haya subrayado */
  background-color: #f0f0f0;
  border-radius: 5px;
  padding: 5px 8px;
  margin: 5px;
  display: inline-block;
  border: 1px solid #ccc;
  font-size: 12px;
  color: #333;
  cursor: default;
}

.row-descripcion{
  min-height: 160px;
}
