.main {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
}


/* WHITE THEME */
.main.white-theme {
  background-color: rgb(0, 0, 0);
  background-image: radial-gradient(ellipse 80% 50% at 50% -20%, rgba(255, 255, 255, 0.76), #e4e3e3);
  color: black;
}

/* BLACK THEME */
.main.black-theme {
  background-color: rgb(255, 255, 255);
  background-image: radial-gradient(ellipse 80% 50% at 50% -10%, #0e1217, hsl(0, 0%, 2%));
  color: white;
  /* background-image:radial-gradient(ellipse 80% 50% at 50% -20%, rgb(83 0 0), hsl(0deg 0% 0%)) */
}

.main.black-theme p {
  color: rgb(209 213 219 /1)
}

.main.black-theme .highlight {
  color: rgb(254 240 138 / 1)
}


.section {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 9rem;
}

.container-section {
  max-width: 650px;
  display: flex;
  flex-direction: column;
}

.section h1 {
  font-weight: bold;
  font-size: 32px;
}

.section h2 {
  font-weight: 600;

}