.header {
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    z-index: 1;
    margin-top: 5px;
    top: 0;
    width: 100%;
}

nav {
    padding: 5px 15px;
    border: 2px solid #2c2c2c;
    background-color: #1a1a1a;
    color: #ffffff;
    font-size: 0.9rem;
    
    border-radius: 30px;
    transition: all 0.4s ease;
    outline: none;
    position: relative;
    overflow: hidden;
    font-weight: bold;

}

nav a {
    text-decoration: none;
    color: white; /* Color por defecto */
    margin: 0 15px;
    position: relative;
    transition: color 0.3s ease; /* Animación suave para el color */
}

/* Estilo para enlaces no activos */
nav a:not(.active) {
    color: lightgray; /* Color más claro para enlaces inactivos */
}

nav a::after {
    content: '';
    position: absolute;
    width: 100%;
    height: 2px; /* altura del subrayado */
    background: white; /* color del subrayado */
    left: 0;
    bottom: -5px; /* espacio entre el enlace y el subrayado */
    transform: scaleX(0);
    transition: transform 0.3s ease;
    transform-origin: left; /* animación desde la izquierda */
}

nav a:hover::after,
nav a.active::after {
    transform: scaleX(1); /* activa la animación al hover o cuando está activo */
}

nav a:hover {
    color: white; /* Cambiar a blanco al hacer hover */
}

.btn-change-theme {
    border: none;
    outline: none;
    background: none;

}